export interface IYeomanConfigurationData {
  set: (key: string, params: Object) => void;
  get: (key: string) => any; /* tslint:disable-line:no-any */
}

// tslint:disable-next-line:typedef
const _strings = {
  libraryName: 'libraryName',
  framework: 'framework',
  version: 'version',
  libraryId: 'libraryId',
  environment: 'environment'
};

/**
 * This class wraps an untyped property bag with types for
 * the generator.
 */
export class YeomanConfiguration {
  private static _store: IYeomanConfigurationData;

  public static setStore(yeomanConfiguration: IYeomanConfigurationData): void {
    YeomanConfiguration._store = yeomanConfiguration;
  }

  public static get libraryName(): string {
    return YeomanConfiguration._store.get(_strings.libraryName);
  }
  public static set libraryName(libraryName: string) {
    YeomanConfiguration._store.set(_strings.libraryName, libraryName);
  }

  public static get version(): string {
    return YeomanConfiguration._store.get(_strings.version);
  }
  public static set version(version: string) {
    YeomanConfiguration._store.set(_strings.version, version);
  }

  public static get libraryId(): string {
    return YeomanConfiguration._store.get(_strings.libraryId);
  }
  public static set libraryId(libraryId: string) {
    YeomanConfiguration._store.set(_strings.libraryId, libraryId);
  }

  public static get environment(): 'spo' | 'onprem' {
    return YeomanConfiguration._store.get(_strings.environment);
  }

  public static set environment(environment: 'spo' | 'onprem') {
    YeomanConfiguration._store.set(_strings.environment, environment);
  }
}